import BGImg3 from "../../assets/img/bg/ap-bg.png";
import React, { useState } from "react";
import Img from "../../assets/img/bg/open-box-img.png";
import emailjs from 'emailjs-com';
import moment from "moment";
import "moment/locale/vi";

const Appointment = () => {
  const [selectedDate, setSelectedDate] = useState("");
  
  function formatDate(dateString) {
    const date = new Date(dateString);
    const formattedDate = `${date.getDate().toString().padStart(2, '0')}/${(date.getMonth() + 1).toString().padStart(2, '0')}/${date.getFullYear()}`;
  
    // Kt hp cc thng tin  to chui c nh dng dd/mm/yyyy
    return formattedDate;
  }

  const defaultDate = new Date().toLocaleString("vi-VN", {
    timeZone: "Asia/Ho_Chi_Minh",
  });

  function sendEmail(e) {
    e.preventDefault();

    const { fullname, email, phone, datetime, service, message } = e.target.elements;

    const messageBody = `
      Name: ${fullname.value}\n
      Email: ${email.value}\n
      Phone: ${phone.value}\n
      Datetime: ${formatDate(datetime.value)}\n
      Service: ${service.value}\n
      Message: ${message.value}\n
    `;

    emailjs.send('service_kzsj3ro', 'template_a1k0pc3', {
      to_email: 'nguyentiendung.it.2912@gmail.com',
      message: messageBody
    }, '9AnWc2G0cZZLyKsSJ')
      .then((result) => {
        console.log(result.text);
      }, (error) => {
        console.log(error.text);
      });

    e.target.reset();
  }


  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      const formEle = document.querySelector("form")
      const form = new FormData(formEle)
      fetch("https://script.google.com/macros/s/AKfycbyo_OFEXj6qrxCs0iIb5eeA92wQFuWnFjWi8f8TCpUARm66UnekCyr1gdnN6T17V7Bf/exec",
      {
        method: "POST",
        body: form
      })
      sendEmail(event)
      alert("t lch hn thnh cng! Chng ti s lin h bn sm nht")
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <>
      <section
        id="booking"
        className="booking-area p-relative pt-120 pb-120"
        style={{
          backgroundSize: "cover",
          backgroundPosition: "center",
          backgroundRepeat: "no-repeat",
          backgroundImage: `url(${BGImg3})`,
        }}
      >
        <div className="container book-appointment-form">
          <div className="row align-items-center">
            <div className="col-lg-6">
              <div className="contact-bg">
                <div className="section-title center-align">
                  <h5>LIN H</h5>
                  <h2>T LCH HN</h2>
                  <p>
                    Lin h t lch ngay  nhn u i cc k hp dn. Hoc gi ngay hotline <span style={{color: "red"}}>0868 990 167</span>  nhn c t vn v cc dch v khc.
                  </p>
                </div>
                <form
                  onSubmit={handleSubmit}
                  className="contact-form mt-30"
                >
                  <div className="row">
                    <div className="col-lg-6">
                      <div className="contact-field p-relative c-name mb-20">
                      <label htmlFor="fullname" className="placeholder">H v tn</label>
                        <input
                          type="text"
                          id="fullname"
                          name="fullname"
                          placeholder="Nhp h v tn ca bn"
                          required
                        />
                      </div>
                    </div>
                    <div className="col-lg-6">
                      <div className="contact-field p-relative c-subject mb-20">
                      <label htmlFor="email" className="placeholder">Email</label>
                        <input
                          type="text"
                          id="email"
                          name="email"
                          placeholder="Nhp a ch email ca bn"
                          required
                        />
                      </div>
                    </div>
                    <div className="col-lg-6">
                      <div className="contact-field p-relative c-subject mb-20">
                      <label htmlFor="phone" className="placeholder">S in thoi</label>
                        <input
                          type="tel"
                          id="phone"
                          name="phone"
                          placeholder="Nhp s in thoi ca bn"
                          required
                        />
                      </div>
                    </div>
                    <div className="col-lg-6">
                      <div className="contact-field p-relative c-subject mb-20">
                        <label htmlFor="datetime" className="placeholder">Ngy t lch v</label>
                        <input
                          type="datetime-local"
                          id="datetime"
                          name="datetime"
                          defaultValue={defaultDate}
                        />
                      </div>
                    </div>
                    <div className="col-lg-12">
                      <div className="contact-field p-relative c-subject mb-30">
                      <label htmlFor="service" className="placeholder">Dch v</label>
                        <select
                          className="custom-select"
                          id="service"
                          name="service"
                          aria-label="Example select with button addon"
                        >
                          <option> La chn dch v</option>
                          <option value="Massage body, Massage mt, Tm trng">Massage body, Massage mt, Tm trng</option>
                          <option value="Gi u thng v Gi u dng sinh">Gi u thng v Gi u dng sinh</option>
                          <option value="iu tr mn, Nm, Tn nhang, Ln kim, So r">iu tr mn, Nm, Tn nhang, Ln kim, So r</option>
                          <option value="Chm sc qung thm mt">Chm sc qung thm mt</option>
                          <option value="Phun mi collagen, Phum my, Phun mi">Phun mi collagen, Phum my, Phun mi</option>
                          <option value="iu tr cng bng da">iu tr cng bng da</option>
                          <option value="Ty nt rui, Cy collagen ti, Trit lng">Ty nt rui, Cy collagen ti, Trit lng</option>
                          <option value="V nhiu dch v khc">V nhiu dch v khc</option>
                        </select>
                        <i className="fa fa-angle-down"></i>
                      </div>
                    </div>
                    <div className="col-lg-12">
                      <div className="contact-field p-relative c-message mb-30">
                      <label htmlFor="message" className="placeholder">Ghi ch thm yu cu</label>
                        <textarea
                          name="message"
                          id="message"
                          cols="30"
                          rows="10"
                          placeholder="Ghi ch thm"
                        ></textarea>
                      </div>
                      <div className="slider-btn">
                        <button
                          type="submit"
                          className="btn ss-btn"
                          data-animation="fadeInRight"
                          data-delay=".8s"
                        >
                          <span>t lch hn</span>
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
            <div className="col-lg-6">
              <div className="opening-time">
                <div className="open-img">
                  <img src={Img} alt="icon01" />
                </div>
                <h3>Gi m ca</h3>
                <div className="text">
                  <div className="left-text">Th 2 - Th 6:</div>
                  <div className="right-text">09:00  21:00 </div>
                </div>
                <div className="text">
                  <div className="left-text">Th 7 - Ch nht:</div>
                  <div className="right-text">09:00  22:00 </div>
                </div>
                <div className="text">
                  a ch: 138 ng 30/4, Ph Ho, Th Du Mt, Bnh Dng.
                </div>
                <div className="discount">
                  Lin h t lch hn  c nhn cc u i mi nht.
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Appointment;
